


.introduce{
  width: 100%;
  height: 480px;
  background-image: url('../../images/main-lot.jpg');
  background-position: 100% 40%;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  z-index: 0;
  display: flex;
  /* justify-content: flex-end; */
  align-items: center;
  box-sizing: border-box;
  padding: 10px 90px;
}

.text_box{
  display: flex;
  flex-direction: column;
  flex-basis: 400px;
  color: #fdfeff;
  
  /* height: 90%; */
}

.title {
  font-family: "Montserrat-Bold";
  font-size: 42px;
  letter-spacing: 2px;
  z-index: 2;
  margin: 0;
}

.subtitle{
  font-family: "Montserrat-Semibold";
  font-size: 23px;
  letter-spacing: 1px;
  z-index: 2;
  margin: 40px 0 0;
}

.link{
  display: block;
  width: 280px;
  height: 70px;
  padding: 22px;
  box-sizing: border-box;
  background-color:  rgba(255, 255, 255, 0.8);
  border-radius: 40px;
  font-family: "Montserrat-Bold";
  font-size: 24px;
  text-transform: uppercase;
  list-style: none;
  text-decoration: none;
  text-align: center;
  z-index: 2;
  color: #465e72;
  margin: 36px 0 0;

}

.introduce::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.data{
  display: flex;
  justify-content: space-between;
  padding: 60px 90px;
}

.data_item{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #465e72;
  position: relative;
  z-index: 0;
}

.data_nums{
  font-family: "Kanit-Regular";
  font-size: 68px;
  opacity: .9;
  margin: 0;
}

.liner{
  display: block;
  background-color: #fdfeff;
  border-bottom: 2px solid #465e72;
  width: 100%;
  height: 4px;
  position: absolute;
  margin: 0;
  top: 58px;
  left: 0;
  right: 0;
  z-index: 2;
}

.data_text{
  color: #354848;
  opacity: .9;
  font-family: "Montserrat-Semibold";
  font-size: 18px;
  margin: 0;
  text-align: center;
}